  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fadeLeft {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fadeRight {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.on-load {
  transform: translateY(0) !important;
  opacity: 1 !important;
}

